.app-wrapper {
    width: 1250px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
    "header header"
    "nav content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: content;
    background-color: rgba(130, 100, 210, 0.91);
}