.profileInfo {
    width: 80%;
    margin: 0 auto;
    padding: 10px;
    border: 1px black solid;
    border-radius: 6px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.jubHunting {
    display: flex;
    flex-direction: column;
}
.professionalSkills {
    display: flex;
    margin-top: 15px;
    color: black;
}
.professionalSkills span {
    margin-right: 10px;
    font-weight: bold;
    background: linear-gradient(to right, #114799, #a638ef);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
.contacts {
    margin-top: 20px;
}
.link {
    display: flex;
    align-items: end;
    margin: 30px 0 10px 10px;
    color: black;
}
.link span {
    font-size: 16px;
    background: linear-gradient(to right, #114799, #a638ef);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
.toggleChangeMode{
    margin: 0 auto;
    position: relative;
    display: block;
    color: black;
    background-color: rgba(140, 140, 140, 0.58);
    font-size: 14px;
    border: 2px solid rgba(24, 150, 72, 0.67);
    padding: 2px 4px;
    cursor: pointer;
    border-radius: 5px;
    text-transform: uppercase;
    overflow: hidden;
    transition: 1s height, width ease, 0.1s transform ease;
    z-index: 10;
}
.toggleChangeMode:active {
    transform: translate(0em, .4em);
}
.toggleChangeMode::before{
    background: #2df59e;
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    transition: all 0.3s ease;
    z-index: -1;
}
.toggleChangeMode::before{
    width: 100%;
    height: 0%;
    transform: translate(-50%,-50%) rotate(45deg);
}
.toggleChangeMode:hover::before{
    height: 730%;
}
.formSubmit {
    position: relative;
    display: block;
    color: #ffffff;
    background-color: rgba(140, 140, 140, 0.58);
    border: 2px solid rgba(2, 132, 169, 0.76);
    padding: 2px 4px;
    cursor: pointer;
    border-radius: 5px;
    overflow: hidden;
    transition: 1s height, width ease, 0.1s transform ease;
    z-index: 10;
}
.formSubmit::before {
    background: #f6d605;
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    transition: all 0.6s ease;
    z-index: -1;
}
.formSubmit::before{
    width: 100%;
    height: 0;
}
.formSubmit:hover::before {
    height: 380%;
}